export default {
  props: ['index'],
  data: function data() {
    return {
      dialog: false,
      drawer: null,
      dataSocket: {}
    };
  },
  created: function created() {
    var vm = this;
    vm.$nextTick(function () {
      var params = vm.$router.history.current.params;
      vm.index = params['index'];
    });
  },
  computed: {
    items: function items() {
      return this.$store.getters.getDeliverableTypes;
    },
    snackbarerror: {
      // getter
      get: function get() {
        return this.$store.getters.getsnackbarerror;
      },
      // setter
      set: function set(newValue) {
        this.$store.commit('setsnackbarerror', newValue);
      }
    },
    snackbarsocket: {
      // getter
      get: function get() {
        return this.$store.getters.getsnackbarsocket;
      },
      // setter
      set: function set(newValue) {
        this.$store.commit('setsnackbarsocket', newValue);
      }
    }
  },
  watch: {
    '$route': function $route(newRoute, oldRoute) {
      var vm = this;
      var currentQuery = newRoute.query;
      var params = newRoute.params;
      vm.index = params['index'];
      console.log('newRoute', newRoute);
      console.log('currentQuery', currentQuery);
    }
  },
  methods: {
    redirectFilter: function redirectFilter(val) {
      this.$router.push(val + '?state_change=' + Math.floor(Math.random() * (100 - 1 + 1)) + 1);
    },
    closeError: function closeError() {
      this.$store.commit('setsnackbarerror', false);
    },
    reloadPage: function reloadPage() {
      window.location.reload(true);
    },
    deliverableRouter: function deliverableRouter(item) {
      console.log(item);
    }
  }
};