import DatetimePicker from './DatetimePicker.vue';
import axios from 'axios';
export default {
  props: ['id', 'datainput'],
  components: {
    DatetimePicker: DatetimePicker
  },
  data: function data() {
    return {
      isCallBack: true,
      config: {},
      pullOk: true,
      pullCounter: 0,
      valid: true,
      loading: false,
      data: {},
      cmOptions: {
        tabSize: 4,
        mode: 'text/javascript',
        theme: 'base16-light',
        lineNumbers: true,
        line: true
      },
      rules: {
        required: function required(value) {
          return !!value || 'Bắt buộc phải nhập.';
        },
        number: function number(value) {
          var pattern = /^\d+$/;
          return pattern.test(value) || 'Bắt buộc phải nhập kiểu số.';
        },
        email: function email(value) {
          var pattern = /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
          return pattern.test(value) || 'Sai định dạng thư điện tử.';
        }
      },
      dataForm: ''
    };
  },
  created: function created() {
    var vm = this;

    if (String(vm.id) !== '0') {
      vm.data = vm.datainput;

      try {
        vm.dataForm = JSON.parse(vm.data['formData']);
      } catch (error) {}
    } else {
      vm.processDataSource();
    }
  },
  computed: {
    detailForm: function detailForm() {
      var detailDynamic = this.$store.getters.getContentFileSimple; // console.log('detailDynamicSimple', detailDynamic)

      if (detailDynamic === '') {
        return [];
      } else {
        return detailDynamic;
      }
    },
    dataSocket: {
      // getter
      get: function get() {
        return this.$store.getters.dataSocket;
      },
      // setter
      set: function set(newValue) {
        this.$store.commit('setdataSocket', newValue);
      }
    },
    pullCounterOrg: function pullCounterOrg() {
      return this.$store.getters.pullCounter;
    },
    activeBindFormData: function activeBindFormData() {
      return this.$store.getters.getActiveBindFormData;
    }
  },
  updated: function updated() {
    var vm = this;
    vm.$nextTick(function () {
      if (vm.isCallBack) {
        vm.isCallBack = false;
        vm.processDataSource();
        setTimeout(function () {
          if (String(vm.id) !== '0') {
            vm.data = vm.datainput;

            try {
              vm.dataForm = JSON.parse(vm.data['formData']);
            } catch (error) {}
          }

          if (vm.pullCounterOrg === 0) {
            vm.pullOk = true;
          }

          setTimeout(function () {
            vm.pullOk = true;
          }, 1000);
        }, 500);
      }
    });
  },
  mounted: function mounted() {
    var vm = this;
    vm.processDataSource();
  },
  watch: {
    data: {
      handler: function handler(val) {
        // console.log('watch data', val)
        var vm = this;
        vm.$store.commit('setChangeFormData', val);
        vm.$store.commit('setActiveBindFormData', !vm.activeBindFormData);
      },
      deep: true
    },
    datainput: function datainput(val) {
      var vm = this; // console.log('val DataInput', val)

      if (String(vm.id) !== '0') {
        vm.data = val;

        try {
          vm.dataForm = JSON.parse(vm.data['formData']);
        } catch (error) {}
      }
    }
  },
  methods: {
    clearLoading: function clearLoading() {
      this.loading = false;
    },
    processRules: function processRules(rulesStr) {
      return eval('( ' + rulesStr + ' )');
    },
    processChangeDataSource: function processChangeDataSource(data, item) {
      var vm = this;

      if (item.hasOwnProperty('concatina')) {
        vm.pullOk = false;
        vm.pullCounter = vm.pullCounter + 1;
      }
    },
    processDataSourceVerify: function processDataSourceVerify() {
      var vm = this;

      var _loop = function _loop(key) {
        if (vm.detailForm[key].hasOwnProperty('datasource_api') && vm.detailForm[key].hasOwnProperty('datasource_key')) {
          if (vm.data.hasOwnProperty(vm.detailForm[key]['model']) && String(vm.data[vm.detailForm[key]['model']]).startsWith("[")) {
            vm.data[vm.detailForm[key]['model']] = JSON.parse(vm.data[vm.detailForm[key]['model']]);
          }
        }

        if (String(vm.id) === '0' && vm.detailForm[key]['model'] === 'subject') {
          vm.detailForm[key]['model'] = '  ';
          setTimeout(function () {
            vm.detailForm[key]['model'] = 'subject';
          }, 200);
        }
      };

      for (var key in vm.detailForm) {
        _loop(key);
      }
    },
    processDataSource: function processDataSource() {
      var vm = this;

      var _loop2 = function _loop2(key) {
        if (vm.detailForm[key].hasOwnProperty('datasource_api') && vm.detailForm[key].hasOwnProperty('datasource_key')) {
          // vm.pullOk = false
          vm.pullCounter = vm.pullCounter + 1; // console.log('vm.pullCounter 1', vm.pullCounter)

          var apiURL = vm.detailForm[key]['datasource_api'];

          if (vm.detailForm[key]['dependency'] && vm.detailForm[key].hasOwnProperty('pk')) {
            apiURL = apiURL + '?pk' + '=' + vm.id + '&col=' + vm.detailForm[key]['pk'];
          }

          var param = {
            headers: {
              groupId: vm.getScopeGroupId()
            }
          };
          axios.get(apiURL, param).then(function (response) {
            var seriable = response.data;

            if (seriable['data']) {
              vm.data[vm.detailForm[key]['datasource_key']] = seriable['data'];
            } else {
              vm.data[vm.detailForm[key]['datasource_key']] = seriable;
            }

            if (vm.detailForm[key]['value'] && String(vm.id) === '0') {
              vm.data[vm.detailForm[key]['model']] = vm.detailForm[key]['value'];
            }

            vm.processDataSourceVerify();
          }).catch(function (xhr) {});
        }
      };

      for (var key in vm.detailForm) {
        _loop2(key);
      }
    },
    parseDate: function parseDate(arg) {
      if (arg) {
        var date = new Date(Number(arg));
        return "".concat(date.getDate().toString().padStart(2, '0'), "/").concat((date.getMonth() + 1).toString().padStart(2, '0'), "/").concat(date.getFullYear());
      } else {
        return '';
      }
    },
    checkVisible: function checkVisible(item, data) {
      if (item['visible']) {
        var visibleItems = String(item['visible']).split(',');
        return visibleItems.filter(function (i) {
          return i === data['deliverableType'];
        }).length > 0;
      } else {
        return true;
      }
    }
  },
  filters: {
    parseDateFilter: function parseDateFilter(arg) {
      if (arg) {
        var date = new Date(Number(arg));
        return "".concat(date.getDate().toString().padStart(2, '0'), "/").concat((date.getMonth() + 1).toString().padStart(2, '0'), "/").concat(date.getFullYear());
      } else {
        return '';
      }
    }
  }
};