import Vue from 'vue';
import $ from 'jquery';
import { BbatTableEditorComponent, BbatTableEditorComponentSimple, ViewPdf, ViewLogs, AttachedFileTemplate } from '@/components';
import toastr from 'toastr';
import { UploaderPlugin } from '@syncfusion/ej2-vue-inputs';
import { detach } from '@syncfusion/ej2-base';
Vue.use(UploaderPlugin);
export default {
  props: ['index', 'id'],
  components: {
    BbatTableEditorComponentSimple: BbatTableEditorComponentSimple,
    BbatTableEditorComponent: BbatTableEditorComponent,
    ViewPdf: ViewPdf,
    ViewLogs: ViewLogs,
    AttachedFileTemplate: AttachedFileTemplate
  },
  data: function data() {
    return {
      drawer: null,
      offsetTop: 0,
      offsetCheck: 400,
      deName: '',
      valid: false,
      active: -1,
      loading: false,
      dataSocket: {},
      tempCounter: 0,
      detail: {},
      showComponent: false,
      formId: '',
      editDeliverable: false,
      extensions: '.pdf',
      dialogPDF: false,
      dialogPDFLoading: false,
      fileEntryIdAttachs: [],
      pageAttachs: 1
    };
  },
  created: function created() {
    var vm = this;
    vm.$nextTick(function () {
      // set permissionUser
      if (vm.items.length > 0) {
        vm.$store.commit('setUserPermission', vm.items[vm.index]['moderator']);
      } // 


      vm.editDeliverable = String(vm.id) === '0' ? true : false;
      vm.$store.dispatch('getDeliverableTypes').then(function (result) {
        setTimeout(function () {
          vm.formId = vm.items[vm.index]['formScriptFileId'];
          vm.deName = ''; // vm.$store.dispatch('getContentFile', formId)

          vm.showComponent = false;
          vm.$store.dispatch('getDeliverableById', vm.id).then(function (result) {
            if (String(vm.id) !== '0') {
              vm.detail = result; // test multiple fileAttachs
              // vm.detail['fileAttachs'] = '1300487,1289275'
              // 
            }

            vm.deName = vm.detail['deliverableName'];
            vm.showComponent = true;
            vm.$store.dispatch('getContentFileSimple');
            vm.tempCounter = vm.pullCounter;
          });
        }, 100);
      });
    });
  },
  watch: {
    '$route': function $route(newRoute, oldRoute) {
      var vm = this;

      if (vm.isConnected) {
        // console.log('watch editor')
        vm.isConnected = false;
        var formId = vm.items[vm.index]['formScriptFileId'];
        vm.deName = ''; // vm.$store.dispatch('getContentFile', formId)

        vm.showComponent = false;
        vm.$store.dispatch('getDeliverableById', vm.id).then(function (result) {
          if (String(vm.id) !== '0') {
            vm.detail = result; // test multiple fileAttachs
            // vm.detail['fileAttachs'] = '1300487,1289275'
            // 
          }

          vm.deName = vm.detail['deliverableName'];
          vm.showComponent = true;
          vm.$store.dispatch('getContentFileSimple');
          vm.tempCounter = vm.pullCounter;
        });
      }
    },
    items: function items(val) {
      var vm = this; // set permissionUser

      if (vm.items.length > 0) {
        vm.$store.commit('setUserPermission', vm.items[vm.index]['moderator']);
      } // 

    }
  },
  computed: {
    items: function items() {
      return this.$store.getters.getDeliverableTypes;
    },
    userPermission: function userPermission() {
      return this.$store.getters.getUserPermission;
    },
    pullCounter: {
      // getter
      get: function get() {
        return this.$store.getters.pullCounter;
      },
      // setter
      set: function set(newValue) {
        this.$store.commit('setpullCounter', newValue);
      }
    },
    isConnected: {
      // getter
      get: function get() {
        return this.$store.getters.getisConnected;
      },
      // setter
      set: function set(newValue) {
        this.$store.commit('setisConnected', newValue);
      }
    }
  },
  mounted: function mounted() {
    var vm = this;
    $('.e-upload').css('display', 'none');
    $('#templateupload').unbind();
    $('#templateupload').change(function () {
      setTimeout(function () {
        vm.loading = true;
        var uploadData = {
          id: vm.id
        };
        vm.$store.dispatch('uploadSingleFile', uploadData).then(function (result) {
          setTimeout(function () {
            toastr.success('Tải giấy phép lên thành công');
            vm.loading = false;
            vm.$store.dispatch('getDeliverableById', vm.id).then(function (result) {
              vm.detail = result;
              vm.editDeliverable = false;
              vm.showComponent = true;
              vm.$refs.viewpdf.pullPDF(vm.detail['fileEntryId']);
            });
          }, 2000);
        }).catch(function () {
          toastr.error('Tải giấy phép lên thất bại');
          vm.loading = false;
        });
      }, 200);
    });
  },
  methods: {
    onScroll: function onScroll(e) {
      this.offsetTop = window.pageYOffset || document.documentElement.scrollTop;
      this.offsetCheck = document.getElementsByTagName('BODY')[0].offsetHeight - this.offsetTop; // console.log('this.offsetCheck', this.offsetCheck)
    },
    goBack: function goBack() {
      window.history.back();
    },
    backToList: function backToList() {
      var vm = this; // console.log('backToList')

      var current = vm.$router.history.current;
      var newQuery = current.query;
      var currentPath = current.path;
      var queryString = '?';
      newQuery['state_change'] = '0';
      newQuery['renew'] = '';

      for (var key in newQuery) {
        if (newQuery[key] !== '' && newQuery[key] !== 'undefined' && newQuery[key] !== undefined) {
          queryString += key + '=' + newQuery[key] + '&';
        }
      }

      queryString += 'renew=' + Math.floor(Math.random() * (100 - 1 + 1)) + 1;
      vm.$router.push({
        path: '/danh-sach-giay-to/' + vm.index + queryString
      });
    },
    saveToData: function saveToData(cmd) {
      var vm = this;

      if (vm.$refs.form.validate()) {
        // 
        try {
          var field = window.$('#formDelivert').alpaca('get').childrenByPropertyId;

          if (field) {
            for (var prop in field) {
              if (field[prop].isRequired() && field[prop].getValue() === '') {
                toastr.clear();
                toastr.error(field[prop].options.title ? field[prop].options.title + ' là trường dữ liệu bắt buộc' : field[prop].options['name'] + ' là trường dữ liệu bắt buộc');
                return;
              }
            }
          }
        } catch (error) {} // 


        vm.loading = true;
        var bbatForm = vm.$refs.bbatForm;
        var submitDataObject = {};

        if (bbatForm !== null && bbatForm !== undefined && bbatForm !== 'undefined') {
          submitDataObject['formData'] = {};
          submitDataObject['formData'] = bbatForm.getFormData();
        }

        submitDataObject['deliverableType'] = vm.items[vm.index]['typeCode']; // console.log('submitDataObject', submitDataObject)

        if (cmd === -1) {
          vm.detail = {};
        } else {
          if (String(vm.id) !== '0') {
            submitDataObject['deliverableCode'] = vm.detail['deliverableCode'];
            submitDataObject['deliverableId'] = vm.detail['deliverableId'];
          }

          vm.$store.dispatch('createDeliverable', submitDataObject).then(function (data) {
            if (String(vm.id) === '0') {
              vm.loading = false; // vm.$refs.attachedObj.doUploadLate(data['createDeliverable']['deliverableId'])

              vm.backToList();
            } else {
              setTimeout(function () {
                toastr.success('Cập nhật thành công');
                vm.loading = false;
                vm.$store.dispatch('getDeliverableById', vm.id).then(function (result) {
                  vm.detail = result;
                  vm.editDeliverable = false;
                  vm.showComponent = true;
                  setTimeout(function () {
                    vm.$refs.viewpdf.pullPDF(vm.detail['fileEntryId']);
                  }, 200);
                });
              }, 2000);
            }
          }).catch(function () {
            vm.loading = false;
          });
        }
      }
    },
    viewFileAttach: function viewFileAttach(detail) {
      var vm = this;
      vm.fileEntryIdAttachs = String(detail['fileAttachs']).split(',');
      vm.loading = true;
      vm.dialogPDFLoading = true;
      vm.$store.dispatch('viewPDF', vm.fileEntryIdAttachs[0]).then(function (result) {
        vm.loading = false;
        vm.dialogPDFLoading = false;
        vm.dialogPDF = true;
        document.getElementById('pdfViewerListComponent').src = result;
      }).catch(function () {
        vm.loading = false;
        vm.dialogPDFLoading = false;
      });
    },
    changePage: function changePage(page) {
      var vm = this;
      var index = Number(page) - 1;
      vm.dialogPDFLoading = true;
      vm.$store.dispatch('viewPDF', vm.fileEntryIdAttachs[index]).then(function (result) {
        vm.dialogPDFLoading = false;
        document.getElementById('pdfViewerListComponent').src = result;
      }).catch(function () {
        vm.dialogPDFLoading = false;
      });
    },
    uploadFileDeliverable: function uploadFileDeliverable() {
      var vm = this;
      document.getElementsByClassName('e-file-select-wrap')[0].querySelector('button').click();
      return false;
    },
    getUser: function getUser(roleItem) {
      var vm = this;
      var roles = vm.$store.getters.getUser.role;

      if (!roles) {
        return false;
      }

      var roleExits = roles.findIndex(function (item) {
        return String(item).indexOf(roleItem) >= 0;
      });
      return roleExits >= 0;
    }
  }
};