import Vue from 'vue';
import { UploaderPlugin } from '@syncfusion/ej2-vue-inputs';
import { detach } from '@syncfusion/ej2-base';
Vue.use(UploaderPlugin);
export default {
  data: function data() {
    return {
      tempShow: true,
      loadingRemove: false,
      loading: false,
      fileTemplateData: [],
      fileTemplateTotal: 0,
      pathHidden: {
        saveUrl: '',
        removeUrl: ''
      },
      path: {
        saveUrl: '',
        removeUrl: ''
      },
      extensions: '.pdf',
      dropArea: "dropArea",
      rawData: [],
      className: ''
    };
  },
  props: ['pk', 'auto'],
  created: function created() {
    var vm = this;
    vm.$nextTick(function () {
      vm.loadFileTemplate();
    });
  },
  mounted: function mounted() {
    var _this = this;

    this.path = {
      saveUrl: '/o/v1/opencps/users/upload/opencps_deliverable/org.opencps.deliverable.model.OpenCPSDeliverableFileEntryId' + '/' + this.pk,
      removeUrl: '/' + this.pk
    };
    this.className = 'org.opencps.deliverable.model.OpenCPSDeliverableFileEntryId';

    document.getElementById('browse').onclick = function () {
      document.getElementsByClassName('e-file-select-wrap')[0].querySelector('button').click();
      return false;
    };

    document.getElementById('dropArea').onclick = function (args) {
      if (args.target.classList.contains('e-file-delete-btn')) {
        for (var i = 0; i < _this.$refs.uploadObj.getFilesData().length; i++) {
          if (args.target.closest('li').getAttribute('data-file-name') === _this.$refs.uploadObj.getFilesData()[i].name) {
            _this.$refs.uploadObj.remove(_this.$refs.uploadObj.getFilesData()[i]);
          }
        }
      } else if (args.target.classList.contains('e-file-remove-btn')) {
        detach(args.target.closest('li'));
      }
    };
  },
  methods: {
    doUploadLate: function doUploadLate(pkInput) {
      var vm = this;

      if (vm.$refs.uploadObj.getFilesData().length > 0) {
        vm.path = {
          saveUrl: '/o/v1/opencps/users/upload/opencps_deliverable/org.opencps.deliverable.model.OpenCPSDeliverableFileEntryId' + '/' + pkInput,
          removeUrl: '/' + pkInput
        };
        var uploadData = {
          id: pkInput
        };
        vm.$store.dispatch('uploadSingleFile', uploadData);
      }
    },
    addHeaders: function addHeaders(args) {
      var vm = this;
      args.currentRequest.setRequestHeader('Token', vm.getAuthToken());
      args.currentRequest.setRequestHeader('groupId', vm.getScopeGroupId());
    },
    loadFileTemplate: function loadFileTemplate() {
      var vm = this;
      var filter = {
        className: vm.className,
        pk: vm.pk
      };
      vm.$store.dispatch('getAttachFileData', filter).then(function (result) {
        vm.fileTemplateData = result.data;
        vm.fileTemplateTotal = result.total;
        var rightAttachedCounter = document.getElementById('rightAttachedCounter');

        if (rightAttachedCounter !== null && rightAttachedCounter !== undefined && vm.fileTemplateTotal > 0) {
          rightAttachedCounter.innerHTML = 'Tổng số: ' + vm.fileTemplateTotal + ' ';
        }
      }).catch(function (reject) {
        console.log(reject);
      });
    },
    onSuccess: function onSuccess() {
      var _this2 = this;

      setTimeout(function () {
        document.getElementById('dropArea').querySelectorAll(".e-upload-success").forEach(function (e) {
          return e.parentNode.removeChild(e);
        });

        _this2.loadFileTemplate();
      }, 2000);
    },
    onFileRemove: function onFileRemove(args) {
      args.postRawFile = false;
    },
    processDeleteFileAttach: function processDeleteFileAttach(item) {
      var vm = this;
      item['className'] = vm.className;

      if (confirm('Bạn có chắc muốn xoá bản ghi này?')) {
        vm.loadingRemove = true;
        vm.$store.dispatch('removeServiceFileTemplate', item).then(function () {
          vm.loadingRemove = false;
          vm.loadFileTemplate();
        }).catch(function (reject) {
          vm.loadingRemove = false;
          alert('Xoá file xảy ra lỗi.' + reject);
        });
      }
    },
    processDownloadFileAttach: function processDownloadFileAttach(item) {
      var vm = this;
      vm.loading = true;
      item['className'] = vm.className;
      vm.$store.dispatch('downloadServiceFileTemplate', item).then(function () {
        vm.loading = false;
      }).catch(function (reject) {
        vm.loading = false;
        alert('Tải file xảy ra lỗi.' + reject);
      });
    }
  }
};