import DatetimePicker from './DatetimePicker.vue';
export default {
  props: ['id', 'formid', 'datainput'],
  components: {
    DatetimePicker: DatetimePicker
  },
  data: function data() {
    return {
      pullCounter: 0,
      isCallBack: true,
      config: {},
      pullOk: true,
      valid: true,
      loading: false,
      data: {},
      cmOptions: {
        tabSize: 4,
        mode: 'text/javascript',
        theme: 'base16-light',
        lineNumbers: true,
        line: true
      },
      rules: {
        required: function required(value) {
          return !!value || 'Bắt buộc phải nhập.';
        },
        number: function number(value) {
          var pattern = /^\d+$/;
          return pattern.test(value) || 'Bắt buộc phải nhập kiểu số.';
        },
        email: function email(value) {
          var pattern = /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
          return pattern.test(value) || 'Sai định dạng thư điện tử.';
        }
      },
      formTempalate: '',
      active: false
    };
  },
  computed: {
    detailForm: function detailForm() {
      var vm = this;
      var detailDynamic = this.$store.getters.getContentFile;
      vm.formTempalate = detailDynamic;

      if (detailDynamic === '') {
        return [];
      } else {
        return detailDynamic;
      }
    },
    alpacaFormData: function alpacaFormData() {
      var vm = this;
      var control = window.$('#formDelivert').alpaca('get');
      var formData = control.getValue();
      console.log('alpacaFormData -+-+', formData);

      if (formData) {
        return formData;
      } else {
        return {};
      }
    },
    pullCounterOrg: function pullCounterOrg() {
      return this.$store.getters.pullCounter;
    },
    formData: function formData() {
      return this.$store.getters.getChangeFormData;
    },
    activeBindFormData: function activeBindFormData() {
      return this.$store.getters.getActiveBindFormData;
    }
  },
  updated: function updated() {
    var vm = this;
    vm.$nextTick(function () {});
  },
  watch: {
    activeBindFormData: function activeBindFormData() {
      var vm = this; // var runBind
      // function runBindData () {
      //   runBind = setTimeout(function () {
      //     vm.bindFormData(vm.formData)
      //   }, 500)
      // }
      // clearTimeout(runBind)
      // runBindData()
    }
  },
  mounted: function mounted() {
    var vm = this;
    vm.$nextTick(function () {
      vm.$store.dispatch('getContentFile', vm.formid).then(function (result) {
        // console.log('formScript --->', result)
        vm.formTempalate = result;
        window.$('#formDelivert').empty();
        var formScript, formData;
        /* eslint-disable */

        if (result) {
          formScript = result;
        } else {
          formScript = {};
        } // console.log('form-Data', vm.datainput)


        if (String(vm.id) !== '0' && vm.datainput) {
          formData = eval('(' + vm.datainput + ')');
        } else {
          formData = {};
        }
        /* eslint-disable */


        formScript.data = formData;
        window.$('#formDelivert').alpaca(formScript); // 
      });
    });
  },
  methods: {
    clearLoading: function clearLoading() {
      this.loading = false;
    },
    processRules: function processRules(rulesStr) {
      return eval('( ' + rulesStr + ' )');
    },
    processChangeDataSource: function processChangeDataSource(data, item) {
      var vm = this;

      if (item.hasOwnProperty('concatina')) {
        vm.pullOk = false;
        vm.pullCounter = vm.pullCounter + 1;
        vm.$store.commit('setpullCounter', vm.pullCounter);
        vm.$socket.sendObj({
          type: 'api',
          cmd: 'get',
          respone: item.concatina['datasource_key'],
          api: item.concatina['datasource_api'] + '?' + item.concatina['query'] + '=' + data,
          headers: {
            'Token': vm.getAuthToken(),
            'groupId': vm.getScopeGroupId(),
            'USER_ID': vm.getUserId()
          }
        });
      }
    },
    processDataSourceVerify: function processDataSourceVerify() {
      var vm = this;

      for (var key in vm.detailForm) {
        if (vm.detailForm[key].hasOwnProperty('datasource_api') && vm.detailForm[key].hasOwnProperty('datasource_key')) {
          if (vm.data.hasOwnProperty(vm.detailForm[key]['model']) && String(vm.data[vm.detailForm[key]['model']]).startsWith("[")) {
            vm.data[vm.detailForm[key]['model']] = JSON.parse(vm.data[vm.detailForm[key]['model']]);
          }
        }
      }
    },
    processDataSource: function processDataSource() {
      var vm = this;

      for (var key in vm.detailForm) {
        if (vm.detailForm[key].hasOwnProperty('datasource_api') && vm.detailForm[key].hasOwnProperty('datasource_key')) {
          vm.pullOk = false;
          vm.pullCounter = vm.pullCounter + 1;
          vm.$store.commit('setpullCounter', vm.pullCounter);
          var apiURL = vm.detailForm[key]['datasource_api'];

          if (vm.detailForm[key]['dependency'] && vm.detailForm[key].hasOwnProperty('pk')) {
            apiURL = apiURL + '?pk' + '=' + vm.id + '&col=' + vm.detailForm[key]['pk'];
          }

          vm.$socket.sendObj({
            type: 'api',
            cmd: 'get',
            respone: vm.detailForm[key]['datasource_key'],
            api: apiURL,
            headers: {
              'Token': vm.getAuthToken(),
              'groupId': vm.getScopeGroupId(),
              'USER_ID': vm.getUserId()
            }
          });
        }
      }
    },
    getFormData: function getFormData() {
      var vm = this;
      var control = window.$('#formDelivert').alpaca('get');
      var formData = control.getValue();

      if (formData) {
        return formData;
      } else {
        return {};
      }
    },
    bindFormData: function bindFormData(form_data) {
      var vm = this;
      console.log('form_data_bind', form_data);
      var formDataRender = form_data;
      window.$('#formDelivert').empty();
      var formScript, formData;
      /* eslint-disable */

      if (vm.formTempalate) {
        formScript = vm.formTempalate;
      } else {
        formScript = {};
      }

      if (formDataRender) {
        formData = formDataRender;

        for (var key in formData) {
          if (String(key).toLowerCase().indexOf('date') >= 0 && formData[key] && String(formData[key]).indexOf('/') < 0) {
            // console.log('key', key, vm.convertDate(formData[key]))
            formData[key] = vm.convertDate(formData[key]);
          } else {// console.log('key 2', key, formData[key])
          }
        } // console.log('form_data 123', formData)

      } else {
        formData = {};
      }
      /* eslint-disable */


      formScript.data = formData;
      window.$('#formDelivert').alpaca(formScript); // console.log('vm.formTempalate ->', formScript)
      // 
    },
    convertDate: function convertDate(val) {
      if (Number(val) > 0) {
        var date = new Date(val);
        return "".concat(date.getDate().toString().padStart(2, '0'), "/").concat((date.getMonth() + 1).toString().padStart(2, '0'), "/").concat(date.getFullYear());
      } else {
        return '';
      }
    }
  }
};